import {apiCommon} from "../components/api/apiLocalStorage";
import {Dispatch} from "redux";
import {GlobalStateType} from "./store-redux";


const SET_THEME = "myApp/dark-light-reducer/SET_THEME"; //  

type setThemeActionType = {type: typeof SET_THEME, themeUpdate:"light" | "dark"  }
export const setTheme = (themeUpdate:"light" | "dark"):setThemeActionType => { //   
  return {type: SET_THEME, themeUpdate }
};

type ActionTypes = setThemeActionType


let initialState = { //   
  themeBLL: "light" as "light" | "dark", //   bll  
}
type initialStateType = typeof initialState

let themeReducer = (state:initialStateType = initialState, action:ActionTypes):initialStateType => {//  
  let stateCopy:initialStateType; //      
  switch (action.type) {
    case SET_THEME: //   
      stateCopy = {
        ...state, //   
        themeBLL: action.themeUpdate, //    BLL
      }
      return stateCopy; //     
    default:
      return state; //     
  }
}

export const setThemeThunkCreator = (theme1:"light" | "dark") => {//    LocalStorage
  return async (dispatch:Dispatch<ActionTypes>, getState: () => GlobalStateType) => { //     LocalStorage
    const response1 = await apiCommon.putTheme1(theme1)  //    localStorage
    if (response1) {
      dispatch(setTheme(response1))  //   localStorage    store
    }

  }
}
export const getThemeThunkCreator = () => {//    LocalStorage
  const getThemeThunk = async (dispatch:Dispatch<ActionTypes>, getState: () => GlobalStateType) => { //     LocalStorage
    const response1 = await apiCommon.getTheme1()  //    localStorage
    if (response1) {
      dispatch(setTheme(response1))  //   localStorage    store
    }

  }
  return getThemeThunk
}


export default themeReducer;










