import {postFeedBack22} from "../components/api/api";
import {apiFeedBackDataType} from "../types/commonTypes";
import {Dispatch} from "redux";
import {GlobalStateType} from "./store-redux";

const SET_FEED_BACK_STATUS = "myApp/feedback-reducer/SET_FEED_BACK_STATUS"; //     feedback

type setFeedBackStatusActionType =  {type: typeof SET_FEED_BACK_STATUS, feedBackStatus:string}
export const setFeedBackStatus = (feedBackStatus:string):setFeedBackStatusActionType => { //    feedBackStatus
  return {type: SET_FEED_BACK_STATUS, feedBackStatus}
};

type initialStateType = { feedBackStatus: string }
let initialState:initialStateType = { //   
  feedBackStatus: "" //    (feedBack) -       
}

type ActionTypes = setFeedBackStatusActionType

let feedBackReducer = (state:initialStateType = initialState, action:ActionTypes):initialStateType => {//  
  let stateCopy:initialStateType; //      
  switch (action.type) {
    case SET_FEED_BACK_STATUS: //   feedBackStatus
      stateCopy = {
        ...state,
        feedBackStatus: action.feedBackStatus
      }
      return stateCopy; //     
    default:
      return state; //     
  }
}

export const postFeedBackThunkCreator2 = (data:apiFeedBackDataType) => {//   
  return async (dispatch:Dispatch<ActionTypes>, getState: () => GlobalStateType) => { //   
    await postFeedBack22(data) //
        .then(() => dispatch(setFeedBackStatus("    ")))//    BLL "    "
        .catch((err:object) => dispatch(setFeedBackStatus((err.toString()))));//          BLL
  };
}

export default feedBackReducer;
