import {getAuthMeThunkCreator} from "./auth-reducer";
import {getThemeThunkCreator} from "./theme-reducer";
import {GlobalStateType} from "./store-redux";
import {ThunkAction} from "redux-thunk";

const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  
const APP_INITIAL_STATE = "myApp/app-reducer/APP_INITIAL_STATE"; //   
const SET_PATCH = "myApp/app-reducer/SET_PATCH"; //    URL
const SET_PAGE_WIDTH = "myApp/app-reducer/SET_PAGE_WIDTH"; //   

type setInitialisedAppType = {type: typeof SET_INITIALISED_APP}
export const setInitialisedApp = ():setInitialisedAppType => { //     
  return {type: SET_INITIALISED_APP}
};

type appInitialStateActionType = {type: typeof APP_INITIAL_STATE}
export const appInitialState = ():appInitialStateActionType => { //    
  return {type: APP_INITIAL_STATE}
};
type setPatchActionType = {type: typeof SET_PATCH, patch:string }
export const setPatch = (patch:string):setPatchActionType => { //    
  return {type: SET_PATCH, patch}
};
type setPageWidthActionType = {type: typeof SET_PAGE_WIDTH, PageWidth:number }
export const setPageWidth = (PageWidth:number):setPageWidthActionType => { //    
  return {type: SET_PAGE_WIDTH, PageWidth}
};

type ActionTypes = appInitialStateActionType | setInitialisedAppType | setPatchActionType | setPageWidthActionType

type initialStateType = typeof initialState

let initialState = { //     
  initialisedApp: false, //   ?
  patch: "",
  PageWidth: document.documentElement.scrollWidth
}

let appReducer = (state:initialStateType = initialState, action:ActionTypes):initialStateType => {//  
  let stateCopy:initialStateType; //      
  switch (action.type) {
    case SET_INITIALISED_APP: //   
      stateCopy = {
        ...state, //   
        initialisedApp: true, //      true
      }
      return stateCopy; //     
    case APP_INITIAL_STATE: //    
      stateCopy = initialState
      return stateCopy; //     
    case SET_PATCH: //   
      stateCopy = {
        ...state, //   
        patch: action.patch, //      true
      }
      return stateCopy; //     
    case SET_PAGE_WIDTH: //    
      stateCopy = {
        ...state, //   
        PageWidth: action.PageWidth, //      true
      }
      return stateCopy; //     
    default:
      return state; //     
  }
}
type ThunkType = ThunkAction<
    void,    //    
    GlobalStateType,    //    redux
    unknown,    //   
    ActionTypes //   ActionCreator
    >
export const initialisedAppThunkCreator = ():ThunkType => {//   
  return (dispatch, getState) => { //    
    const promise1 = dispatch(getAuthMeThunkCreator()) //   
    const promise2 = dispatch(getThemeThunkCreator()) //  
    Promise.all([promise1, promise2]) //    
        .then(() => {
          dispatch(setInitialisedApp()) //     true
        })
  };
}

export default appReducer;










