import {apiProfile} from "../components/api/api";
import {dialogsInitialState, dialogsInitialStateType} from "./dialogs-reducer";
import {profileInitialState, profileInitialStateActionType} from "./profile-reducer";
import {USERS_INITIAL_STATE, usersInitialState} from "./users-reducer";
import {GlobalStateType} from "./store-redux";
import {ThunkAction} from "redux-thunk";
import {getProfileType} from "../components/api/apiTypes";
import {ResultCodeEnum, ResultCodeEnumCaptcha} from "../components/api/enum";
import {NulableType} from "../types/commonTypes";

type usersInitialStateActonType = { type: typeof USERS_INITIAL_STATE }

const SET_MY_DATA = "myApp/auth-reducer/SET_MY_DATA"; //        (ID, Email, login, isAuth)
const AUTH_INITIAL_STATE = "myApp/auth-reducer/AUTH_INITIAL_STATE"; //   
const SET_CAPTCHA_URL = "myApp/auth-reducer/SET_CAPTCHA_URL"; //  URL 
const SET_LOGIN_ERROR= "myApp/auth-reducer/SET_LOGIN_ERROR"; //   
const SET_MY_PROFILE = "myApp/auth-reducer/SET_MY_PROFILE"; //      

export type setMyProfileActionType = {type: typeof SET_MY_PROFILE, myProfile: getProfileType}
export const setMyProfile = (myProfile:getProfileType):setMyProfileActionType => { //       
    return {type: SET_MY_PROFILE, myProfile}
};

type setAuthDataActionType = {
    type: typeof SET_MY_DATA,
    id:number,
    email:string,
    login:string,
    isAuth: boolean
}
export const setAuthData = (id:number, email:string, login:string, isAuth:boolean):setAuthDataActionType => {
    //     ID, Email, login
    return {type: SET_MY_DATA, id, email, login, isAuth}
};

type authInitialStateActionType = {type: typeof AUTH_INITIAL_STATE}
export const authInitialState = ():authInitialStateActionType => { //     
    return {type: AUTH_INITIAL_STATE}
};

type setCaptchaURLActionType = {type: typeof SET_CAPTCHA_URL, captchaURL: string}
export const setCaptchaURL = (captchaURL:string):setCaptchaURLActionType => { //    URL    
    return {type: SET_CAPTCHA_URL, captchaURL}
};

type setLoginErrorActionType = {type: typeof SET_LOGIN_ERROR, loginError: string}
export const setLoginError = (loginError: string):setLoginErrorActionType => { //      
    return {type: SET_LOGIN_ERROR, loginError}
};

type ActionTypes = setLoginErrorActionType | setCaptchaURLActionType | authInitialStateActionType |
    setAuthDataActionType | setMyProfileActionType | dialogsInitialStateType | profileInitialStateActionType | usersInitialStateActonType

let initialState = { //      
    myId: 0 as number, //  ID  
    myEmail: "" as string,//  Email  
    myLogin: "" as string,//    
    isAuth: false, //  
    myProfile: null as NulableType<getProfileType>, //     
    captchaURL: "" as string, // URL   5  
    loginError: "" as string, //    
}
export type initialStateAuthType = typeof initialState
let authReducer = (state:initialStateAuthType = initialState, action:ActionTypes):initialStateAuthType => { //     
    let stateCopy:initialStateAuthType; //      
    switch (action.type) {
        case SET_MY_DATA: //    id, email, login
            stateCopy = {
                ...state,
                myId: action.id,
                myEmail: action.email,
                myLogin: action.login,
                isAuth: action.isAuth,
            }

            return stateCopy; //     
        case SET_MY_PROFILE: //     
            stateCopy = {
                ...state,
                myProfile: action.myProfile
            }

            return stateCopy; //     
        case AUTH_INITIAL_STATE: //    
            stateCopy = initialState
            return stateCopy; //     
        case SET_CAPTCHA_URL: //   URL   
            stateCopy = {
                ...state,
                captchaURL: action.captchaURL
            }
            return stateCopy; //     
        case SET_LOGIN_ERROR: //      
            stateCopy = {
                ...state,
                loginError: action.loginError
            }

            return stateCopy; //     
        default:
            return state; //     
    }
}
type ThunkType = ThunkAction<
    void,    //    
    GlobalStateType,    //    redux
    unknown,    //   
    ActionTypes //   ActionCreator
    >
export const getAuthMeThunkCreator = ():ThunkType => {//  ?.    
    return async (dispatch, getState) => {
        const response1 = await apiProfile.getAuthMe() //  ?
        if (response1.resultCode === ResultCodeEnum.Success) { //   / 5 
            dispatch(setAuthData(
                response1.data.id, //     ID
                response1.data.email, //     
                response1.data.login, //     
                true //    
            ))//    

            const response2 = await apiProfile.getProfile(response1.data.id)//     

            dispatch(setMyProfile(response2))//     
        }
        if (response1.resultCode !== ResultCodeEnum.Success) { //  
            dispatch(authInitialState()) //   
        }
    };
}

export const postLoginThunkCreator = (email:string, password:string, rememberme?:boolean, captcha?:string):ThunkType => {
    //  
    return async (dispatch, getState) => { //    
        const response = await apiProfile.postLogin(email, password, rememberme, captcha) //       
        if (response.resultCode === ResultCodeEnum.Success) { //     
            dispatch(getAuthMeThunkCreator()) //      
        } else { //      
            const message =  //    message -   
                !response.messages[0] //     
                    ? "no responce from server" //   
                    : response.messages[0] //     
            if (response.resultCode === ResultCodeEnumCaptcha.CaptchaIsReqiured) { //         
                dispatch(getCaptchaThunkCreator())
            }
            dispatch(setLoginError(message)) //    
        }
    };
}

export const deleteLoginThunkCreator = ():ThunkType => {//  
    return async (dispatch, getState) => { //    
        const response = await apiProfile.deleteLogin() //    
        if (response.resultCode === ResultCodeEnum.Success) { //    
            setTimeout(() => {

                dispatch(dialogsInitialState())//    

                dispatch(authInitialState())//    

                dispatch(profileInitialState())//    

                dispatch(usersInitialState())//  UsersBS  

            }, 300)
        } else {
            console.log(response.messages) //      
        }
    };
}

export const getCaptchaThunkCreator = ():ThunkType => {//   
    return async (dispatch, getState) => { //    
        const response2 = await apiProfile.getCaptcha() //  
        dispatch(setCaptchaURL(response2.url)) //      
    };
}

export default authReducer;










