import {apiDialogs} from "../components/api/apiLocalStorage";
import {GlobalStateType} from "./store-redux";
import {ThunkAction} from "redux-thunk";
import {dialogs2Type, messages2Type, NulableType} from "../types/commonTypes";

const DIALOGS_INITIAL_STATE = "myApp/dialogs-reducer/DIALOGS_INITIAL_STATE";  //   
const SET_MESSAGES = "myApp/dialogs-reducer/SET_MESSAGES";  //     
const DIALOG_USER_ID = "myApp/dialogs-reducer/DIALOG_USER_ID"; //  ID ,   
const DIALOG_LAST_UPDATE_TIME = "myApp/dialogs-reducer/DIALOG_LAST_UPDATE_TIME"; //  ID ,   
const DIALOG_USER_FOLLOWED = "myApp/dialogs-reducer/DIALOG_USER_FOLLOWED"; //  follow/unfollow   (  )
const GET_MY_DIALOG_LIST = "myApp/dialogs-reducer/GET_MY_DIALOG_LIST"; //   

export type dialogsInitialStateType =  {type: typeof DIALOGS_INITIAL_STATE}
export const dialogsInitialState = ():dialogsInitialStateType => { //    
  return {type: DIALOGS_INITIAL_STATE}
};

type setMessagesActionType = {type: typeof SET_MESSAGES, updatedMessages:Array<messages2Type>}
export const setMessages = (updatedMessages:Array<messages2Type>):setMessagesActionType => { //       messages2
  return {type: SET_MESSAGES, updatedMessages}
};

type setdialogUserIDActionType = {type: typeof DIALOG_USER_ID, dialogUserID:number}
export const setdialogUserID = (dialogUserID:number):setdialogUserIDActionType => { //       messages2
  return {type: DIALOG_USER_ID, dialogUserID}
};

type setDialogLastUpdateTimeActionType = {type: typeof  DIALOG_LAST_UPDATE_TIME, dialogLastUpdateTime:string}
export const setDialogLastUpdateTime = (dialogLastUpdateTime:string):setDialogLastUpdateTimeActionType => { //       
  return {type: DIALOG_LAST_UPDATE_TIME, dialogLastUpdateTime}
};

type setDialogUserFollowedActionType = {type: typeof DIALOG_USER_FOLLOWED, dialogUserFollowed:boolean}
export const setDialogUserFollowed = (dialogUserFollowed:boolean):setDialogUserFollowedActionType => { //       
  return {type: DIALOG_USER_FOLLOWED, dialogUserFollowed}
};

type getMyDialogListActionType={type: typeof GET_MY_DIALOG_LIST, myDialogList:Array<dialogs2Type>}
export const getMyDialogList = (myDialogList:Array<dialogs2Type>):getMyDialogListActionType => { //      
  return {type: GET_MY_DIALOG_LIST, myDialogList}
};

type ActionTypes = getMyDialogListActionType | setDialogUserFollowedActionType | setDialogLastUpdateTimeActionType |
    setdialogUserIDActionType | setMessagesActionType | dialogsInitialStateType

let initialState = { //    
  messages2: null as NulableType<Array<messages2Type>>, //    ,      Array<messages2Type>
  dialogUserID: 0 as number, // ID    
  dialogUserFollowed: false, //  followed? ()
  dialogLastUpdateTime: "" as string, //     
  dialogs2: null as NulableType<Array<dialogs2Type>> //  .    LocalStorage    
}

type initialStateType = typeof initialState

let dialogsReducer = (state:initialStateType = initialState, action: ActionTypes):initialStateType => { //  
  let stateCopy:initialStateType; //      
  switch (action.type) {
    case DIALOGS_INITIAL_STATE: //        
      stateCopy = initialState;
      return stateCopy
    case SET_MESSAGES: //        
      stateCopy = {
        ...state,
        messages2: action.updatedMessages, //  
      }
      return stateCopy
    case DIALOG_USER_ID: //    ID ,   
      stateCopy = {
        ...state,
        dialogUserID: action.dialogUserID, //  ID ,   
      }
      return stateCopy
    case DIALOG_LAST_UPDATE_TIME: //        
      if (state.dialogLastUpdateTime !== action.dialogLastUpdateTime) { // ,        (  )
        stateCopy = {
          ...state,
          dialogLastUpdateTime: action.dialogLastUpdateTime, //       
        }
        return stateCopy
      }
      return state //    ,    
    case DIALOG_USER_FOLLOWED: //    follow/unfollow   (   )
      stateCopy = {
        ...state,
        dialogUserFollowed: action.dialogUserFollowed, //   follow/unfollow   (   )
      }
      return stateCopy
    case GET_MY_DIALOG_LIST: //     
      stateCopy = {
        ...state,
        dialogs2: action.myDialogList, //    
      }
      return stateCopy
    default:
      return state;
  }
}
type ThunkType = ThunkAction<
    void,    //    
    GlobalStateType,    //    redux
    unknown,    //   
    ActionTypes //   ActionCreator
    >

export const getDialogsThunkCreator = (myId:number, userId:number):ThunkType => {//    
  return async (dispatch, getState) => {//    
    const updatedMessages = await apiDialogs.getDialog(myId, userId)
    dispatch(setMessages(updatedMessages))
  }
}
export const sendDialogsThunkCreator =
    (formDataNewMessage:string, myId:number, MyName:string, MyPhoto:string, userId:number):ThunkType => {//     
  return async (dispatch, getState) => {//      
    const updatedMessages = await apiDialogs.postDialog(formDataNewMessage, myId, MyName, MyPhoto, userId)
    dispatch(setMessages(updatedMessages))
  }
}

export const getDialogLastUpdateTimeTnkCrt = (myId:number, userId:number):ThunkType => {//    
  return async (dispatch, getState) => {//    
    const dialogLastUpdateTime = await apiDialogs.getUpdateTime(myId, userId) //     
    dispatch(setDialogLastUpdateTime(dialogLastUpdateTime)) //   BLL     
  }
}

export const deleteMessageThunkCreator = (messageID:number, myId:number, userId:number):ThunkType => {//    
  return async (dispatch, getState) => {//     
    const dialogAfterDeleteMessage = await apiDialogs.deleteMessage(messageID, myId, userId) //         
    dispatch(setMessages(dialogAfterDeleteMessage))//      
  }
}


export const getMyDialogListThunkCreator = (myId:number):ThunkType => {//   
  return async (dispatch, getState) => {// 
    const myDialogList = await apiDialogs.getDialogListMyID(myId) //   
    dispatch(getMyDialogList(myDialogList))//     
  }
}

//updateDialogListThunkCreator(myId, response.userId, response.fullName, response.photos.small
export const updateDialogListThunkCreator = (userId1:number, userId2:number, Name2:string, Photo2:string):ThunkType => {
  //   (      ) -   localStorage.
  return async (dispatch, getState) => {// 
    await apiDialogs.updateDialogListUserId(userId1, userId2, Name2, Photo2) //   
  }
}
export const deleteDialogThunkCreator = (dialogId:number, userId1:number, userId2:number):ThunkType => {
  //    
  return async (dispatch, getState) => {// 
    await apiDialogs.deleteDialog(dialogId, userId1, userId2) //      
//      -    
  }
}

export default dialogsReducer;

