import React, {Suspense, useEffect} from "react";
import {Route, Routes} from "react-router-dom";
import ErrorBoundary from "../common/ErrorBoundary/ErrorBoundary";
import Tasks from "../Tasks/Tasks";
import Home from "../Home/Home";
import classes from "./ContentContainer.module.css"
import {useLocation} from "react-router";

const UsersContainer = React.lazy(() => import("../users/UsersContainerFC"))
const ProfileContainer = React.lazy(() => import("../Profile/ProfileContainerFC"))
const DialogsContainer = React.lazy(() => import("../DialogList/DialogListContainer"))
const LoginContainer = React.lazy(() => import("../Login/LoginContainer"))
const News = React.lazy(() => import("../News/News"))
const Rest = React.lazy(() => import("../Rest/Krestiki-Noliki/KrestikiNoliki"))
const StackInfo = React.lazy(() => import("../Info/StackInfoBS"))
const FeedBackContainer = React.lazy(() => import("../FeedBack/FeedBackContainer"))

type ContentContainerType = {
    setPatch: (patch:string)=>void,
    setPageWidth: (PageWidth:number) => void
}
let ContentContainer: React.FC<ContentContainerType> = ({setPatch, setPageWidth}) => { //      

    const location = useLocation()
    useEffect(()=>{ //          
        let patch = location.pathname //   URL  /profile
            .split( "" ) //     ['/', 'd', 'i', 'a', 'l', 'o', 'g', 's', '2', '8', '8', '3', '1',]
            .filter( i => i !== "/" ) //    "/" ['d', 'i', 'a', 'l', 'o', 'g', 's', '2', '8', '8', '3', '1',]
            .join( "" ) //   dialogs  users
        patch = patch.replace(/[0-9]/g, '');//    ID   dialogs
        setPatch(patch)
        //    patch  app-reducer
    },[location, setPatch])

    function setPageWidthLocal() { //    
        const PageWidth1 = document.documentElement.scrollWidth//    
        setTimeout(()=>{ //  
            const PageWidth2 = document.documentElement.scrollWidth //     
            if (PageWidth1===PageWidth2) { //        ,
                setPageWidth(PageWidth1) //    (    )
            }
        }, 300) //      
    }
    window.onresize = setPageWidthLocal;


    return (<div>

        <ErrorBoundary> {/*   ContentContainer*/}
            <Suspense fallback={
                <div>...</div>}> {/* ,    Lazy import   fallback   */}
                <div className={classes.contentContainer}>
                    <Routes> {/*   URL   */}
                        <Route path='' element={<Home/>}/> {/* */}
                        <Route path='/mystack/*' element={<StackInfo/>}/> {/* */}
                        <Route path='/profile/*' element={<ProfileContainer/>}/> {/**/}
                        <Route path='/dialogs/*' element={<DialogsContainer/>}/> {/**/}
                        <Route path='/users/*' element={<UsersContainer/>}/> {/*  UsersBS*/}
                        <Route path='/login/*' element={<LoginContainer/>}/> {/**/}
                        <Route path='/news/*' element={<News/>}/> {/*   hn algonia*/}
                        <Route path='/rest/*' element={<Rest/>}/> {/* */}
                        <Route path='/feedback/*' element={<FeedBackContainer/>}/> {/* */}
                        <Route path='/tasks/*' element={<Tasks/>}/> {/* */}
                    </Routes>
                </div>
            </Suspense>
        </ErrorBoundary>
    </div>)
}
export default ContentContainer
