import {apiUsers} from "../components/api/api";
import {ThunkAction} from "redux-thunk";
import {GlobalStateType} from "./store-redux";
import {Dispatch} from "redux";
import {usersType} from "../components/api/apiTypes";
import {ResultCodeEnum} from "../components/api/enum";

function inferLiteralFromString<T extends string>(arg: T): T {
    return arg
}

const SET_TERM = "myApp/users-reducer/SET_TERM";
export const setTerm = (term: string) => {
    return {type: inferLiteralFromString(SET_TERM), term}
};

const SET_USERS = "myApp/users-reducer/SET_USERS";
const setUsers = (users: Array<usersType>) => {
    return {type: inferLiteralFromString(SET_USERS), users}
};

const SET_CURRENT_PAGE = "myApp/users-reducer/SET_CURRENT_PAGE";
export const setCurrentPage = (currentPage: number) => {
    return {type: inferLiteralFromString(SET_CURRENT_PAGE), currentPage}
};

const TOGGLE_IS_FETCHING = "myApp/users-reducer/TOGGLE_IS_FETCHING";
const toggleIsFetching = (isFetching: boolean) => {
    return {type: inferLiteralFromString(TOGGLE_IS_FETCHING), isFetching}
};

const SET_TOTAL_USERS_COUNT = "myApp/users-reducer/SET_TOTAL_USERS_COUNT";
const setUsersTotalCount = (totalUsersCount: number) => {
    return {type: inferLiteralFromString(SET_TOTAL_USERS_COUNT), totalUsersCount}
};

const TOGGLE_IS_FOLLOWING_PROGRESS = "myApp/users-reducer/TOGGLE_IS_FOLLOWING_PROGRESS";
const toggleIsFollowingProgerss = (isFetching: boolean, id: number) => {
    return {type: inferLiteralFromString(TOGGLE_IS_FOLLOWING_PROGRESS), isFetching, id}
};

const NEED_UPDATE_FRIENDS = "myApp/users-reducer/NEED_UPDATE_FRIENDS";
export const needUpdateFriendsAC = (needUpdateFriends: boolean) => {
    return {type: inferLiteralFromString(NEED_UPDATE_FRIENDS), needUpdateFriends}
};

export const USERS_INITIAL_STATE = "myApp/users-reducer/USERS_INITIAL_STATE";
export const usersInitialState = () => {
    return {type: inferLiteralFromString(USERS_INITIAL_STATE)}
};

const SET_ONLY_FRIENDS = "myApp/users-reducer/SET_ONLY_FRIENDS";//     ,   
export const setOnlyFriends = (onlyFriends: boolean) => { //      ,   
    return {type: inferLiteralFromString(SET_ONLY_FRIENDS), onlyFriends}
};


type ActionTypes =
    ReturnType<typeof setOnlyFriends> | ReturnType<typeof usersInitialState> | ReturnType<typeof needUpdateFriendsAC> |
    ReturnType<typeof toggleIsFollowingProgerss> | ReturnType<typeof setUsersTotalCount> | ReturnType<typeof toggleIsFetching> |
    ReturnType<typeof setCurrentPage> | ReturnType<typeof setUsers> | ReturnType<typeof setTerm>

const initialState = {
    users: [] as Array<usersType>, //     ( )
    pageSize: 100, //       
    totalUsersCount: 0, //     
    currentPage: 1, //      
    isFetching: false, //   ()
    followingInProgress: [] as Array<number>, //   ,    follow/unfollow  disable button
    term: "", //    
    needUpdateFriends: false, // ,    ,  
    onlyFriends: false
    , //     
}
type initialStateType = typeof initialState

const usersReducer = (state: initialStateType = initialState, action: ActionTypes): initialStateType => {
    //     
    let stateCopy: initialStateType; //      
    switch (action.type) {
        case TOGGLE_IS_FOLLOWING_PROGRESS: // disable     (follow/unfollow)
            stateCopy = {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.id] //  follow/unfollow ,    followingInProgress id 
                    : state.followingInProgress.filter( id => id !== action.id )//     OK,  id    followingInProgress
            }
            return stateCopy; //   
        case SET_USERS:
            stateCopy = {...state, users: action.users};
            return stateCopy; //   
        case SET_CURRENT_PAGE:
            stateCopy = {
                ...state,
                currentPage: action.currentPage,
            }
            return stateCopy; //   
        case SET_TERM:
            stateCopy = {
                ...state, //   
                term: action.term //   
            }
            return stateCopy; //   
        case SET_TOTAL_USERS_COUNT:
            stateCopy = {
                ...state,
                totalUsersCount: action.totalUsersCount,
            }
            return stateCopy; //   
        case TOGGLE_IS_FETCHING:
            stateCopy = {
                ...state,
                isFetching: action.isFetching
            }
            return stateCopy; //   
        case NEED_UPDATE_FRIENDS:
            stateCopy = {
                ...state,
                needUpdateFriends: action.needUpdateFriends
            }
            return stateCopy; //   
        case USERS_INITIAL_STATE:
            stateCopy = initialState //    
            return stateCopy; //   
        case SET_ONLY_FRIENDS: //     
            stateCopy = {
                ...state,
                onlyFriends: action.onlyFriends
            }
            return stateCopy; //   
        default:
            return state;
    }
}
type ThunkType = ThunkAction<void,    //    
    GlobalStateType,    //    redux
    unknown,    //   
    ActionTypes //   ActionCreator
    >
export const getUsersThunkCreator //    
    = (currentPage: number, pageSize: number, term: string, friend: boolean, userId: number): ThunkType => {

    return (dispatch, getState) => { //    
        dispatch( toggleIsFetching( true ) ) //    

        apiUsers.getUsers( currentPage, pageSize, term, friend ) //       
            .then( (data) => {
                dispatch( toggleIsFetching( false ) )  //    
                dispatch( setUsers( data.items ) )//     
                dispatch( setUsersTotalCount( data.totalCount ) )//     

                if (userId) { //  /   
                    dispatch( needUpdateFriendsAC( true ) ) //   
                    dispatch( toggleIsFollowingProgerss( false, userId ) )// ID     followingInProgress,  
                }
            } )
    }
}

type responseType = {
    data: object,
    fieldsErrors: Array<string>,
    messages: Array<string>,
    resultCode: number
}

const _followUnfollowFlow = ( //     followThunkCreator/unfollowThunkCreator
    dispatch: Dispatch<ActionTypes>,
    userId: number,
    currentPage: number,
    pageSize: number,
    apiMethod: any,// (userId:number)=>,
    term: string,
    friend: boolean
) => {
    dispatch( toggleIsFollowingProgerss( true, userId ) )// ID     followingInProgress   
    apiMethod( userId )//    // diff apiMethod = postFollow
        .then( (response: responseType) => {
            if (response.resultCode === ResultCodeEnum.Success) {
                // @ts-ignore
                dispatch( getUsersThunkCreator( currentPage, pageSize, term, friend, userId ) )
                //     /  
            }
        } )
}

export const followThunkCreator =
    (userId: number, currentPage: number, pageSize: number, term: string, friend: boolean): ThunkType => {// follow  
        return (dispatch, getState) => {//  follow
            _followUnfollowFlow( dispatch, userId, currentPage, pageSize, apiUsers.postFollow.bind( apiUsers ), term, friend );
        }
    }

export const unfollowThunkCreator =
    (userId: number, currentPage: number, pageSize: number, term: string, friend: boolean): ThunkType => {// unfollow  
        return (dispatch, getState) => {//  unfollow
            _followUnfollowFlow( dispatch, userId, currentPage, pageSize, apiUsers.deleteFollow.bind( apiUsers ), term, friend );
        }
    }
export default usersReducer;





